<template>
  <xdh-map-popup ref="popup" :closable="true" :close-on-click="true" width="300px">
    <div>HTML</div>
  </xdh-map-popup>
</template>

<script>
  /**
   * 气泡弹窗覆盖物组件
   * @module xdh-map-popup
   */

  import XdhMapPopup from '../../../popup'
  // import {getParent, mapReady} from 'utils/util'

  /**
   * 插槽
   * @member slots
   * @property {string} title 定义标题内容
   * @property {string} default 定义主体内容
   */
  export default {
    name: 'XdhMapPopup',
    components: {
      XdhMapPopup
    },
    /**
     * 参数属性
     * @member props
     * @property {string} [width] 窗体宽度
     * @property {string} [height] 窗体高度
     * @property {string} [title] 标题文字
     * @property {string} [icon] 标题文字前的字体图标样式名称
