     *
     */
    props: {
      ...XdhMapPopup.props
    },
    computed: {
      
    },
    methods: {
      
    },
    created() {
     
    },
    mounted() {
      this.refs.popup.show(this.position)
    },
    beforeDestroy() {
    }
  }
</script>
